.nav {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    text-decoration: none;
    color: #292929;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
  }

  &__link:hover {
    width: fit-content;
    padding-bottom: 4px;
    border-bottom: 1px solid $mainColor;
  }
}
