.menu {
  background-color: #e5e6e8;
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    text-decoration: none;
    display: block;
    color: #292929;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    padding-bottom: 16px;
  }

  &__call-to-order {
    text-decoration: none;
    display: block;
    color: #292929;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding-bottom: 4px;
    border-bottom: 1px solid #292929;
    width: fit-content;
  }
}
