.store {
  @include responsive-width;

  .container__store {
    @include on-tablet {
      display: grid;
      grid-template-columns: repeat(6, 80px);
      grid-template-rows: repeat(5, auto);
      gap: 56px 24px;
    }

    @include on-desktop {
      grid-template-columns: repeat(12, 63px);
    }
  }

  &__title {
    font-size: 32px;
    padding-bottom: 48px;

    @include on-tablet {
      font-size: 40px;
      grid-column: 1 / 5;
      grid-row: 1 / 2;
    }

    @include on-desktop {
      font-size: 48px;
      grid-column: 1 / 8;
    }
  }

  &__category {
    padding-bottom: 40px;
    height: auto;

    @include on-tablet {
      grid-column: 1 / -1;

      &--row2 {
        grid-row: 2 / 3;
        grid-column: 1 / 3;
      }
      &--row3 {
        grid-row: 3 / 4;
      }
      &--row4 {
        grid-row: 4 / 5;
      }
    }

    &-text {
      font-size: 18px;

      @include on-tablet {
        font-size: 24px;
        grid-column: 1 / 3;
      }
    }

    &-img {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 20px;

      @include on-tablet {
        display: grid;
        grid-template-columns: repeat(6, 80px);
        grid-column-gap: 24px;
      }

      @include on-desktop {
        grid-template-columns: repeat(12, 63px);
      }

      img {
        border-radius: 4px;
        padding-bottom: 16px;
        width: 100%;
        height: auto;
        object-fit: cover;

        @include hover(transform, scale(110%));
      }

      &--big {
        @include on-tablet {
          grid-column: 1 / 5;
        }

        @include on-desktop {
          grid-column: 1 / 9;
        }
      }

      &--big-right {
        @include on-tablet {
          grid-column: span 4;
        }

        @include on-desktop {
          grid-column: span 8;
        }
      }

      &--small--left {
        @include on-tablet {
          grid-column: 1 / 3;
        }

        @include on-desktop {
          grid-column: 1 / 5;
        }
      }
      &--small {
        @include on-tablet {
          grid-column: span 2;
        }

        @include on-desktop {
          grid-column: span 4;
        }
      }
    }
  }

  &__button {
    @include on-tablet {
      grid-column: 1 / -1;
      grid-row: 5 / 6;
    }

    @include on-desktop {
      grid-column: 1 / 5;
    }
  }
}
