.page {
  font-family: 'Space Grotesk', Arial, Helvetica, sans-serif;
  font-weight: 700;
  scroll-behavior: smooth;
  color: #292929;

  .page:has(.page__menu:target) {
    overflow: hidden;
  }

  button,
  textarea {
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 700;
    font-size: 16px;
  }

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
    }
  }
}
