.about-us {
  @include responsive-width;

  @include on-tablet {
    display: grid;
    grid-template-columns: repeat(6, 80px);
    column-gap: 24px;
  }

  @include on-desktop {
    grid-template-columns: repeat(12, 63px);
  }

  &-title {
    padding-bottom: 24px;

    @include on-tablet {
      font-size: 40px;
      grid-column: 1 / 3;
    }

    @include on-desktop {
      font-size: 48px;
      grid-column: 1 / 4;
    }
  }
  &-text {
    font-family: $smallerFont;
    font-weight: 400;
    font-size: 16px;

    @include on-tablet {
      grid-column: 4 / -1;
    }

    @include on-desktop {
      grid-column: 7 / -1;
    }
  }
}
