.contact-us {
  @include responsive-width;

  @include on-desktop {
    &__container {
      display: grid;
      grid-template-columns: repeat(12, 63px);
      column-gap: 24px;
    }
  }

  &__title {
    margin-bottom: 48px;

    @include on-tablet {
      font-size: 40px;
      margin-bottom: 56px;
    }

    @include on-desktop {
      font-size: 48px;
    }
  }

  &__form {
    margin-bottom: 48px;

    @include on-tablet {
      margin-bottom: 80px;
    }

    @include on-desktop {
      grid-column: 1 / 6;
    }

    .input {
      width: 100%;
      border: none;
      border-radius: 8px;
      background-color: #f8f8fa;
      color: #7e7e83;
      cursor: pointer;
      font-family: $smallerFont;
      font-size: 14px;
      font-weight: 400;
      margin-bottom: 16px;
      padding: 14px 16px;
      outline: none;

      &:hover {
        border: 1px solid #d5d7de;

        &::placeholder {
          color: $mainColor;
        }
      }

      &:focus {
        border: 1px solid $mainColor;
        color: $mainColor;
      }
    }

    .textarea {
      resize: none;
      margin-bottom: 32px;
    }
  }

  &__info {
    @include on-desktop {
      grid-column: 7 / 10;
    }
  }

  &__info-div {
    margin-bottom: 24px;
    font-weight: 400;

    .name {
      color: #9393a3;
      font-size: 14px;
      margin-bottom: 8px;
    }

    .value {
      text-decoration: none;
      font-family: $smallerFont;
      font-size: 16px;
      color: #191919;

      @include hover(color, grey);
    }
  }
}
