.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;

  &__icons {
    display: flex;
    justify-content: space-between;
    gap: 24px;

    @include on-tablet {
      gap: 32px;
    }
  }

  &__icon {
    width: 24px;
    height: 24px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;

    @include on-tablet {
      width: 32px;
      height: 32px;
    }

    @include hover(transform, scale(120%));
  }

  &__logo {
    display: flex;
  }
}
